.home-polls {
  background-color: rgb(50, 72, 143);
  color: white;
}
.home-poll-slider {
  width: 80%;
  margin-left: 10%;
  padding-bottom: 10px;
  margin-bottom: 10px;
}
.home-poll-outer-box {
  padding: 1rem;
}
.home-poll-box {
  background-color: white;
  color: black;
  padding: 1rem;
  border-radius: 0.5rem;
  height: 500px;
}
@media (max-width: 767px) {
  .mobile-hide,
  .instagram-media {
    display: none !important;
  }
  .red-box {
    height: 10rem !important;
  }
  .home-poll-slider {
    width: 100%;
    margin-left: 0;
  }
}
@media (min-width: 767px) {
  .mobile-show {
    display: none !important;
  }
}
.slick-dots li button:before {
  color: white !important;
}
